import { hot } from 'react-hot-loader/root';
import React, { Component } from 'react';
import { render, findDOMNode } from 'react-dom';

import '../css/styles.scss';
import * as Colyseus from 'colyseus.js';

class Main extends Component {
  constructor() {
    super();

    var endpoint = location.protocol.replace("http", "ws") + "//" + location.hostname + ':' + location.port;

    this.colyseus = new Colyseus.Client(endpoint);
    this.room = this.colyseus.join('room');
    this.onUpdateRemote = this.onUpdateRemote.bind(this);
    this.room.onStateChange.add(state => this.onUpdateRemote(state));
    this.state = {
      users: {},
      round: 1,
      inProgress: false,
      score: 0,
      wordCount: 0,
      messages: []
    }
  }

  onUpdateRemote(newState, patches) {
    console.log('new state: ', newState, 'patches: ', patches);
    //this.setState(newState);
  }

  render() {
    return (
      <div className="app">

      </div>
    )
  }
}

render((
  <Main />
), document.getElementById('main'));